import niceTimeDirective from 'shared/nice-date-time/nice-time.directive';

export default angular.module('deskworks.TerminationRequestCtrl', [
  'deskworks.state',
  'deskworks.TerminationRequest',
  niceTimeDirective.name,
])

  .controller('TerminationRequestCtrl', function($scope, $timeout, $translate, $q, $filter, dwAlerts,
                                                 state, helper, recurringCharges, TerminationRequest) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('TERMINATION_REQUEST.PAGE_TITLE'));

    $ctrl.noticeDate = moment();
    $ctrl.getToday = () => moment();
    $ctrl.recurringCharges = recurringCharges.filter(product => product.status.id === 'active' && product.billingFrequency !== 'asNeeded');
    $ctrl.recurringCharge = $ctrl.recurringCharges[0];
    $ctrl.terminateMembership = true;
    $ctrl.terminateCoMembers = true;
    $ctrl.terminateRecurringCharges = true;

    /**
     * Get data for Your Termination Requests list
     */
    $ctrl.fetchRequests = () => TerminationRequest.query(state.getCurrentCenterId(), state.getCurrentUserId())
      .catch(err => dwAlerts.error(err, 'Failed to query termination requests.'))
      .then(result => {
        $ctrl.terminationRequests = result;
        $ctrl.terminationRequests.forEach(request => { request.chargeProductName = getProductName(request); });
      });
    $ctrl.fetchRequests();

    /**
     * Calculate terminationDate
     */
    $ctrl.calcTerminationDate = () => {
      if (!$ctrl.noticeDate) return;
      const days = $ctrl.recurringCharge && $ctrl.recurringCharge.noticePeriod || 30;
      $ctrl.terminationDate = $ctrl.noticeDate.clone().add(days, 'd');
    };
    $ctrl.calcTerminationDate($ctrl.noticeDate);

    /**
     * Submit termination request
     */
    $ctrl.terminate = () => {
      if (helper.pointFirstInvalid('#terminationRequest')) return;

      const data = {
        recurringChargeId: $ctrl.recurringCharge.id,
        noticeDate: $filter('date')($ctrl.noticeDate, 'yyyy-MM-dd'),
        reason: $ctrl.reason,
        terminateMembership: $ctrl.terminateMembership,
        terminateCoMembers: $ctrl.terminateCoMembers,
        terminateRecurringCharges: $ctrl.terminateRecurringCharges,
      };

      return TerminationRequest.create(state.getCurrentCenterId(), state.getCurrentUserId(), data)
        .catch(err => dwAlerts.error(err, 'Failed to save termination request.'))
        .then(() => $ctrl.fetchRequests());
    };

    /**
     * Get product name of the recurring charge
     */
    function getProductName(request) {
      return (recurringCharges.find(charge => charge.id === request.recurringChargeId) || {}).productName;
    }
  });
