/**
 * TerminationRequest - service for termination of user recurring billings
 */
export default angular.module('deskworks.TerminationRequest', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer',
])

  .factory('TerminationRequest', function($resource, deskworksConfig, datesDeserializer) {
    const desDates = datesDeserializer(['createdAt', 'noticeDate']);

    const res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/termination-requests'), {}, {
      query: { method: 'GET', isArray: true, transformResponse: [angular.fromJson, desDates] },
      save: { method: 'POST' },
    });

    return {
      query: (centerId, userId) => res.query({ centerId, userId }).$promise,

      create: (centerId, userId, data) => res.save({ centerId, userId }, data).$promise,
    };
  });
